/**
 * breakpoint-up/down - Change color of a css property based on the currently active theme
 *
 * @param  {size}     min/max size of the screen
 *
 * @example:-
 *
 * HIDE FOR DESKTOP:
 @include breakpoint-down(small) {
    background: red;
 }
 * HIDE FOR MOBILE:
 @include breakpoint-up(medium) {
    background: blue;
 }
 *
 * if you are designing your layout with a mobile-first approach, then use min-width breakpoints and work your way up.
 * if you are designing for larger devices first, then set your default CSS as you normally would and adjust for smaller devices with the max-width approach.
*/
.weekly-picks {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (max-width: 67.125em) {
  .weekly-picks {
    justify-content: center;
    gap: 2.25rem;
  }
}
.weekly-picks__product-cont {
  display: flex;
  gap: 7.6875rem;
}
.weekly-picks__header {
  position: relative;
  font-weight: 900;
  font-size: 40px;
  line-height: 48px;
  margin: 0;
}
.weekly-picks__item {
  margin: 8rem 0;
}
@media (max-width: 67.125em) {
  .weekly-picks__item {
    margin: 3.0625rem 0;
  }
}
.weekly-picks__item-link {
  text-decoration: none;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (max-width: 67.125em) {
  .weekly-picks__item-link {
    display: grid;
    place-items: center;
  }
}
.weekly-picks__price, .weekly-picks__product-title {
  color: #000;
}